<template>
  <div>
    <!-- 搜索结果页-头部导航 -->
    <div class="search-result-container">
      <!-- 点击实现后退效果 -->
      <van-nav-bar
        title="搜索结果"
        left-arrow
        @click-left="$router.go(-1)"
        fixed
      />
    </div>
    <van-list
      v-model="loading"
      :finished="finished"
      finished-text="没有更多了"
      @load="onLoad"
      :immediate-check="false"
    >
      <!-- 文章列表 -->
      <ArtileList
        v-for="obj in articleList"
        :key="obj.art_id"
        :artObj="obj"
        :isShow="false"
        @click.native="itemClickFn(obj.art_id)"
      ></ArtileList>
    </van-list>
  </div>
</template>

<script>
import { searchResultListAPI } from '@/api'
import ArtileList from '@/components/ArtileItem.vue'
export default {
  name: 'SearchResult',
  data () {
    return {
      page: 1,
      articleList: [], // 文章列表
      loading: false, // 加载状态
      finished: false// 是否全部加载完成
    }
  },
  methods: {
    async onLoad () {
      if (this.articleList.length > 0) {
        this.page++
        const res = await searchResultListAPI({
          q: this.$route.params.kw,
          page: this.page
        })
        if (res.data.data.results.length === 0) {
          // 没有更多数据
          this.finished = true
          return
        }
        this.articleList = [...this.articleList, ...res.data.data.results]
        this.loading = false
      }
    },
    // 点击文章单元格组件--跳转路由事件
    itemClickFn (id) {
      this.$router.push({
        path: `/detail?art_id=${id}`
      })
    }
  },
  async created () {
    const res = await searchResultListAPI({
      q: this.$route.params.kw
    })
    console.log('搜索列表：', res)
    this.articleList = res.data.data.results
  },
  components: {
    ArtileList
  }
}
</script>

<style lang="less" scoped>
.search-result-container {
  padding-top: 46px;
}
</style>
